import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import View from '@/components/View'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/view',
      name: 'View',
      component: View
    }
  ]
})



